<template>
  <d2-container>
    <template slot="header">
      <el-select
        v-model="serviceName"
        placeholder="请选择服务"
        size="small"
        filterable
        clearable
      >
        <el-option
          v-for="item in serviceOptions"
          :key="item"
          :label="item"
          :value="item"
        >
        </el-option>
      </el-select>
      <el-button
        class="filter-item"
        type="primary"
        icon="search"
        @click="getTableList"
        size="small"
        >搜索</el-button
      >
    </template>
    <el-button
      class="filter-item"
      style="margin-left: 10px"
      @click="handleCreate"
      type="primary"
      size="mini"
      icon="el-icon-circle-plus"
      >添加</el-button
    >
    <el-table
      :data="tableList"
      size="small"
      stripe
      highlight-current-row
      style="width: 100%; margin-top: 20px"
      v-loading="tableLoading"
      element-loading-text="正在向服务器请求数据"
      element-loading-spinner="el-icon-loading"
      element-loading-background="rgba(0, 0, 0, 0.8)"
    >
      <el-table-column type="index" width="50" label="序号"></el-table-column>
      <el-table-column align="center" prop="ip" label="ip"></el-table-column>
      <el-table-column
        align="center"
        width="200px"
        prop="port"
        label="端口"
      ></el-table-column>
      <el-table-column
        align="center"
        prop="instanceId"
        label="实例ID"
      ></el-table-column>
      <!-- <el-table-column align="center" prop="weight" label="权重"></el-table-column>
      <el-table-column align="center" prop="metadata" label="扩展信息"></el-table-column>
      <el-table-column align="center" prop="marked" label="marked"></el-table-column>
      <el-table-column align="center" prop="valid" label="valid"></el-table-column> -->
      <el-table-column align="center" label="操作" width="150" fixed="right">
        <template slot-scope="scope">
          <el-button size="small" type="success" @click="handleEidt(scope.row)"
            >编辑
          </el-button>
          <el-button size="small" type="danger" @click="handleDelete(scope.row)"
            >删除</el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <!-- <template slot="footer">
    </template> -->

    <!-- 添加 编辑弹窗 -->
    <el-dialog :title="title" :visible.sync="dialogFormVisible" width="500px">
      <el-form
        :model="addForm"
        ref="addForm"
        :rules="FormRules"
        label-width="120px"
        size="small"
      >
        <el-form-item label="ip" prop="ip">
          <el-input
            v-model="addForm.ip"
            placeholder="请输入ip"
            style="width: 300px"
          ></el-input>
        </el-form-item>
        <el-form-item label="类型端口" prop="port">
          <el-input
            v-model="addForm.port"
            placeholder="请输入端口"
            style="width: 300px"
          ></el-input>
        </el-form-item>
        <el-form-item label="服务名" prop="serviceName">
          <el-input
            v-model="addForm.serviceName"
            placeholder="请输入服务名"
            style="width: 300px"
          ></el-input>
        </el-form-item>
        <el-form-item label="是否健康" prop="healthy">
          <el-radio v-model="addForm.healthy" label="true">是</el-radio>
          <el-radio v-model="addForm.healthy" label="false">否</el-radio>
          <!-- <el-input v-model="addForm.healthy" placeholder="请输入"></el-input> -->
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="closeAddDialog" size="small">取 消</el-button>
        <el-button
          v-if="title === '添加实例'"
          type="primary"
          @click="createSubmit('addForm')"
          size="small"
          >确 定</el-button
        >
        <el-button
          v-else
          type="primary"
          @click="updateSubmit('addForm')"
          size="small"
          >确 定</el-button
        >
      </div>
    </el-dialog>
  </d2-container>
</template>

<script>
import {
  fetchGetInstanceList,
  fetchGetServiceList,
  fetchDeleteInstance,
  fetchAddInstance,
  fetchUpdateInstance,
} from "@/api/demo/index";
export default {
  name: "deviceManage",
  data() {
    return {
      tableLoading: false,
      tableList: [],
      total: 0,
      currentPage: 1,
      pageSize: 20,
      serviceOptions: [],
      serviceName: "",
      dialogFormVisible: false,
      title: "添加实例",
      FormRules: {
        ip: [{ required: true, message: "请输入ip", trigger: "blur" }],
        port: [{ required: true, message: "请输入端口", trigger: "blur" }],
        serviceName: [
          { required: true, message: "请输入服务名", trigger: "blur" },
        ],
      },
      addForm: {
        ip: "",
        port: "",
        serviceName: "",
        healthy: false,
      },
    };
  },
  created() {
    this.getService();
  },
  methods: {
    // 获取服务列表
    getService() {
      fetchGetServiceList({
        pageNo: 1,
        pageSize: 50,
      })
        .then((response) => {
          console.log(response);
          this.serviceOptions = response.doms.filter(
            (item) => !item.startsWith("ace")
          );
          if (this.serviceOptions.length > 0) {
            this.serviceName = this.serviceOptions[0];
            this.getTableList();
          }
        })
        .catch((e) => {
          console.log(e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
        });
    },
    // 获取实例数据
    getTableList() {
      fetchGetInstanceList({
        serviceName: this.serviceName,
      })
        .then((response) => {
          this.tableList = response.hosts;
        })
        .catch((e) => {
          console.log(e);
          this.$alert(e, "网络请求发生错误", {
            confirmButtonText: "确定",
            callback: (action) => {},
          });
        });
    },
    handleCreate() {
      this.dialogFormVisible = true;
      this.title = "添加实例";
    },
    handleEidt(row) {
      this.dialogFormVisible = true;
      this.title = "编辑实例";
      // 编辑信息赋值
      this.addForm.ip = row.ip;
      this.addForm.port = row.port;
      this.addForm.serviceName = row.serviceName;
    },
    // 删除实例
    handleDelete(row) {
      this.$confirm("此操作将永久删除, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      }).then(() => {
        fetchDeleteInstance({
          ip: row.ip,
          serviceName: row.serviceName,
          port: row.port,
        }).then((response) => {
          this.getTableList();
        });
      });
    },
    // 添加实例
    createSubmit(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          fetchAddInstance(this.addForm).then(() => {
            this.closeAddDialog();
            this.getTableList();
            this.$notify({
              title: "成功",
              message: "创建成功",
              type: "success",
              duration: 2000,
            });
          });
        } else {
          return false;
        }
      });
    },
    updateSubmit(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          fetchUpdateInstance(this.addForm).then(() => {
            this.closeAddDialog();
            this.getTableList();
            this.$notify({
              title: "成功",
              message: "创建成功",
              type: "success",
              duration: 2000,
            });
          });
        } else {
          return false;
        }
      });
    },
    closeAddDialog() {
      this.dialogFormVisible = false;
      // 表单置空
      if (this.$refs["addForm"]) {
        this.$refs["addForm"].resetFields();
      }
    },
  },
};
</script>
